import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Animal} from './animal';
import 'rxjs/add/operator/retry';
import 'ngx-bootstrap';

@Injectable()
export class DataService {

  private getAllURL = 'http://localhost:8084/animalwebstore/rest/all';
  private getAllSortedAlphabeticallyURL = 'http://localhost:8084/animalwebstore/rest/sA';
  private getAllSortedReverseAlphabeticallyURL = 'http://localhost:8084/animalwebstore/rest/sRA';
  private getAllSortedPriceAscendingURL = 'http://localhost:8084/animalwebstore/rest/sPA';
  private getAllSortedPriceDescendingURL = 'http://localhost:8084/animalwebstore/rest/sRA';
  private getAnimalsTypesURL = 'http://localhost:8084/animalwebstore/rest/aTypes';
  constructor(private http: HttpClient) { }

  getAnimals() {
    return this.http.get<Animal[]>(this.getAllURL);
  }
  getAnimalsAlphabetically() {
    return this.http.get<Animal[]>(this.getAllSortedAlphabeticallyURL);
  }
  getAnimalsReverseAlphabetically() {
    return this.http.get<Animal[]>(this.getAllSortedReverseAlphabeticallyURL);
  }
  getAnimalsSortedPriceAscending() {
    return this.http.get<Animal[]>(this.getAllSortedPriceAscendingURL);
  }
  getAnimalsSortedPriceDescending() {
    return this.http.get<Animal[]>(this.getAllSortedPriceDescendingURL);
  }
  getAnimalsTypes() {
    return this.http.get<String[]>(this.getAnimalsTypesURL);
  }
}



