import { Component, OnInit } from '@angular/core';
import {Animal} from '../animal';
import {DataService} from '../data.service';

@Component({
  selector: 'app-view-center',
  templateUrl: './view-center.component.html',
  styleUrls: ['./view-center.component.css']
})
export class ViewCenterComponent implements OnInit {

  public animals: Animal[] = [];
  constructor(private dataService: DataService) {}
  ngOnInit() {
    console.log(this.animals);
    this.dataService.getAnimals().subscribe(res => {
      console.log(res);
      this.animals = res;
      console.log(this.animals);
    });
  }
  sortPriceAscending() {
    this.dataService.getAnimalsSortedPriceAscending().subscribe(res => {
      console.log(res);
      this.animals = res;
      console.log(this.animals);
    });
  }

  sortPriceDescending() {
    this.dataService.getAnimalsSortedPriceDescending().subscribe(res => {
      console.log(res);
      this.animals = res;
      console.log(this.animals);
    });
  }

  sortAlphabetically() {
    this.dataService.getAnimalsAlphabetically().subscribe(res => {
      console.log(res);
      this.animals = res;
      console.log(this.animals);
    });
  }

  sortReverseAlphabetically() {
    this.dataService.getAnimalsReverseAlphabetically().subscribe(res => {
      console.log(res);
      this.animals = res;
      console.log(this.animals);
    });
  }
}



