import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {ViewCenterComponent} from './view-center/view-center.component';
import {ViewLeftComponent} from './view-left/view-left.component';

const routes: Routes = [
  {path: 'center', component: ViewCenterComponent},
  {path: 'left', component: ViewLeftComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
export const routingComponents = [ViewCenterComponent, ViewLeftComponent]



