import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AppComponent } from './app.component';
import {HttpClientModule} from '@angular/common/http';
import {DataService} from './data.service';
import { ViewTopComponent } from './view-top/view-top.component';
import { ViewRightComponent } from './view-right/view-right.component';
import { ViewBottomComponent } from './view-bottom/view-bottom.component';
import { BsDropdownModule } from 'ngx-bootstrap/dropdown';
import {ModalModule, TooltipModule} from 'ngx-bootstrap';
import {AppRoutingModule, routingComponents} from './app-routing.module';
import {HashLocationStrategy, LocationStrategy} from '@angular/common';

@NgModule({
  declarations: [
    AppComponent,
    ViewTopComponent,
    ViewRightComponent,
    ViewBottomComponent,
    routingComponents
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    BsDropdownModule.forRoot(),
    TooltipModule.forRoot(),
    ModalModule.forRoot(),
    AppRoutingModule
  ],
  providers: [DataService, {provide: LocationStrategy, useClass: HashLocationStrategy}],
  bootstrap: [AppComponent]
})
export class AppModule { }



